import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
import Swal from 'sweetalert2';
export default {
  components: {
    AgGridVue,
    Swal
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      rowData: [],
      searchType: " m.mat_lot",
      // 검색 조건 
      searchText: "",
      // 검색어
      columnDefs: [{
        field: "mat_lot",
        headerName: "lot",
        flex: 1
      }, {
        field: "mat_code",
        headerName: "자재코드",
        flex: 1
      }, {
        field: "mat_name",
        headerName: "자재명",
        flex: 1
      }, {
        field: "storage_code",
        headerName: "창고코드",
        flex: 1
      }, {
        field: "store_name",
        headerName: "창고명",
        flex: 1
      }, {
        field: "mat_check_pass",
        headerName: "입고가능수량",
        flex: 1
      }, {
        field: "check_code",
        headerName: "검수코드",
        flex: 1
      }, {
        field: "store_quantity",
        headerName: "입고량",
        flex: 1
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 5,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.matList();
  },
  methods: {
    close() {
      this.$emit("close");
    },
    matList() {
      axios.get('/api/mat/storeListModal').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    searchMaterials() {
      axios.get('/api/mat/storeListModal', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    async clicked(event) {
      //자재사용여부 만약 있으면 수정 및 삭제 부락
      let result = await axios.get('/api/mat/matUseCheck/' + event.data.mat_lot);
      console.log(result.data.useCheck);
      if (result.data[0].useCheck > 0) {
        Swal.fire({
          title: '실패',
          text: '이미 자재가 소모된 건입니다.',
          icon: "error",
          confirmButtonText: "확인"
        });
        return;
      }
      this.$emit('selectOrder', event.data);
      this.close();
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.matList(); // visible이 true로 바뀌면 쿼리 실행
      }
    }
  }
};