import Swal from 'sweetalert2';
import axios from "axios";
import { AgGridVue } from "ag-grid-vue3";
import StoreList from '@/components/modal/StoreList.vue';
import { useUserStore } from '@/stores/user';
export default {
  components: {
    AgGridVue,
    Swal,
    StoreList
  },
  data() {
    return {
      rowData: [],
      searchType: "mat_name",
      // 검색 조건 
      searchText: "",
      // 검색어
      columnDefs: [{
        field: "mat_code",
        headerName: "자재코드",
        flex: 1
      }, {
        field: "mat_name",
        headerName: "자재명",
        flex: 1
      }, {
        field: "mat_order_code",
        headerName: "발주코드",
        flex: 1
      }, {
        field: "company_name",
        headerName: "업체명",
        flex: 1
      }, {
        field: "check_code",
        headerName: "검수코드",
        hide: true
      }, {
        field: "mat_check_pass",
        headerName: "입고가능수량",
        flex: 1,
        valueFormatter: params => {
          return params.value != null ? `${params.value}개` : '';
        }
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 8,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      },
      info: {
        mat_lot: '',
        emp_name: useUserStore().empName,
        emp_code: useUserStore().id,
        mat_code: '',
        mat_name: '',
        mat_check_pass: '',
        store_quantity: '',
        check_code: '',
        note: '',
        storage_code: ''
      },
      storeListAry: [],
      showModal: false
    };
  },
  mounted() {
    this.readyList();
    this.lotAuto();
    this.storeList();
  },
  computed: {
    formatAleady() {
      if (this.info.mat_check_pass == '') {
        return this.info.mat_check_pass;
      } else {
        return this.info.mat_check_pass + '개';
      }
    }
  },
  watch: {
    'info.store_quantity': function (newVal) {
      if (this.info.mat_check_pass < this.info.store_quantity) {
        this.info.store_quantity = this.info.mat_check_pass;
        alert('입고가능수량보다 입고량이 넘을 수 없습니다.');
      }
    }
  },
  methods: {
    close() {
      this.$emit("close");
    },
    async readyList() {
      //검수대기 항목 조회
      await axios.get('/api/mat/storeReadyList').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    //검색 조회
    async searchReadyList() {
      await axios.get('/api/mat/storeReadyList', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    //등록건수 체크
    async storageCheck() {
      let result = await axios.get('/api/mat/storeCount/' + this.info.mat_lot).catch(err => {
        console.log(err);
      });
      return result.data.countCheck;
    },
    //LOT 자동증가
    async lotAuto() {
      await axios.get('/api/mat/matLotAuto').then(res => {
        this.info.mat_lot = res.data.mat_lot;
      }).catch(err => {
        console.log(err);
      });
    },
    //클릭시 전달
    clicked(event) {
      this.info.mat_name = event.data.mat_name;
      this.info.mat_code = event.data.mat_code;
      this.info.mat_check_pass = event.data.mat_check_pass;
      this.info.check_code = event.data.check_code;
      this.info.emp_code = useUserStore().id;
      this.info.emp_name = useUserStore().empName;
    },
    //창고리스트 조회
    async storeList() {
      await axios.get('/api/mat/storeList').then(res => {
        console.log(res.data);
        this.storeListAry = res.data;
      });
    },
    //등록
    async addStore() {
      //값 다 넣었는지 조회
      if (this.info.store_quantity == '' || this.info.storage_code == '' || this.info.mat_code == '') {
        Swal.fire({
          title: '실패',
          text: '해당하는 값을 입력해주십시오',
          icon: "error",
          confirmButtonText: '확인'
        });
        return;
      }
      //이미등록건인지 조회
      let count = await this.storageCheck();
      if (count > 0) {
        Swal.fire({
          title: '실패',
          text: '이미 등록이 진행된 건 입니다.',
          icon: "error",
          confirmButtonText: '확인'
        });
        return;
      }
      //등록 진행
      await axios.post('/api/mat/storeAdd', this.info).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '성공',
            text: '등록이 성공하였습니다',
            icon: "success",
            confirmButtonText: '확인'
          });
          //초기화
          this.resetValue();
          this.readyList();
          this.lotAuto();
        } else {
          Swal.fire({
            title: '실패',
            text: '등록이 실패하였습니다.',
            icon: "error",
            confirmButtonText: '확인'
          });
        }
      });
    },
    async searchStore() {
      this.showModal = true;
    },
    //값전달
    async orderSelected(event) {
      this.info.mat_lot = event.mat_lot;
      this.info.mat_code = event.mat_code;
      this.info.mat_name = event.mat_name;
      this.info.mat_check_pass = event.mat_check_pass;
      this.info.store_quantity = event.store_quantity;
      this.info.check_code = event.check_code;
      this.info.storage_code = event.storage_code;
    },
    //초기화
    resetValue() {
      this.info.mat_lot = '';
      this.info.mat_code = '';
      this.info.mat_name = '';
      this.info.mat_check_pass = '';
      this.info.store_quantity = '';
      this.info.check_code = '';
      this.info.storage_code = '';
    },
    //수정
    async updateStore() {
      //값 다 넣었는지 조회
      if (this.info.store_quantity == '' || this.info.storage_code == '' || this.info.mat_code == '') {
        Swal.fire({
          title: '실패',
          text: '해당하는 값을 입력해주십시오',
          icon: "error",
          confirmButtonText: '확인'
        });
        return;
      }
      //이미등록건인지 조회
      let count = await this.storageCheck();
      if (count < 1) {
        Swal.fire({
          title: '실패',
          text: '등록이 되지 않은 건입니다.',
          icon: "error",
          confirmButtonText: '확인'
        });
        return;
      }
      await axios.put('/api/mat/storeUpdate', this.info).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '수정',
            text: '수정이 완료되었습니다.',
            icon: "success",
            confirmButtonText: '확인'
          });
          //초기화
          this.resetValue();
          this.readyList();
          this.lotAuto();
        } else {
          Swal.fire({
            title: '수정실패',
            text: '수정이 실패하였습니다',
            icon: "error",
            confirmButtonText: '확인'
          });
        }
      }).catch(err => console.log(err));
    },
    //삭제
    async deleteStore() {
      //이미등록건인지 조회
      let count = await this.storageCheck();
      if (count < 1) {
        Swal.fire({
          title: '실패',
          text: '등록이 되지 않은 건입니다.',
          icon: "error",
          confirmButtonText: '확인'
        });
        return;
      }
      //삭제시작
      await axios.delete('/api/mat/storeDelete/' + this.info.mat_lot).then(res => {
        //삭제 성고
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '삭제',
            text: '삭제기 완료되었습니다.',
            icon: "success",
            confirmButtonText: '확인'
          });
          //초기화
          this.resetValue();
          this.readyList();
          this.lotAuto();
        } else {
          Swal.fire({
            title: '삭제실패',
            text: '삭제가 실패하였습니다',
            icon: "error",
            confirmButtonText: '확인'
          });
        }
      });
    }
  }
};